@mixin boxShadow {
    -webkit-box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.38);
    -moz-box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.38);
    box-shadow: 0px 0px 9px 0px rgba(0,0,0,0.38);
}

$lightGrey: #D3D3D3;

.messages {
    &-outer {
        display: flex;
        width: 100%;
        padding: 10px;
    }

    &-inner {
        width: 100%;
        height: auto;
        border: 1px solid $lightGrey;

        &-top {
            display: flex;
            justify-content: space-between;
            padding: 5px;
            @include boxShadow;
            font-size: 12px;

            &-left {
                &-wrapper {
                    display: flex;

                    li {
                        padding: 5px;
                        font-weight: bold;
                        border-right: 1px solid $lightGrey;
                    }
                }
            }

            &-right {
                align-self: flex-end;

                &-newMsg {
                    &-btn {
                        background-color: #47924A;
                        border: none;
                        border-radius: 2px;
                        padding: 5px;
                        text-align: center;
                        color: #fff;
                        cursor: pointer;

                        &:hover {
                            opacity: .8;
                        }
                    }
                }
            }
        }

        &-main {
            width: 100%;
            height: 520px;
            display: flex;

            &-search {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-top: 5px;
                padding: 20px 20px 20px 10px;
                margin: 5px;
                max-width: 320px;
                width: 100%;
                height: 50px;
                @include boxShadow;             

                &-inner {
                    width: 100%;
                    &-input {
                        width: 100%;
                        font-size: 12px;
                        border: 1px solid $lightGrey;
                        border-radius: 1.5px;
                        padding: 3px;

                        &:focus {
                            outline: none;
                        }
                    }
                }
            }

            &-chat {
                width: 100%;
                height: 520px;

                &-inner {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    border-left: 1px solid $lightGrey;
                    width: 100%;
                    height: 100%;

                    &-field {

                        height: 100%;
                        text-align: center;
                        font-size: 10px;
                        padding: 50px;
                    }

                    &-msgArea {
                        background-color: #F6F7F9;
                        padding: 10px;
                        height: 200px;
                        &-textArea {
                            textarea {
                                width: 100%;
                                font-size: 12px;
                                resize: none;
                                height: 60px;

                                &:focus {
                                    outline: none;
                                }
                            }
                        }

                        &-footer {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            height: auto;
                            padding: 5px;
                            
                            &-uploadFile {
                                display: flex;
                                &-label {
                                    background-color: $lightGrey;
                                    padding: 5px;
                                    border-radius: 2.5px;
                                    cursor: pointer;
                                    font-size: 12px;
                                }
                            }

                            &-send {
                                &-btn {
                                    font-size: 12px;
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}